<template>
  <v-app>
    <v-container fluid>
      <v-row class="car2 mx-1">
        <v-col cols="12" style="font-size: 17px; text-align: center">
          <v-row>
            <h1 class="red--text ml-5">Datos del lote No. {{ lote }}</h1>
            <v-spacer></v-spacer>
            <div align-items="center">
              <h3 v-if="estado == 3" class="mr-10">En Subasta</h3>
              <h3 v-if="estado == 2" class="mr-10">Terminada</h3>
            </div>
          </v-row>
        </v-col>
        <v-col cols="3">
          <v-row class="mx-2">
            <p
              class="font-weight-black black--text"
              style="font-size: 20px; text-align: center"
            >
              Clase de lote:
              <span class="font-weight-black red--text">{{ clase }}</span>
            </p>
          </v-row>
          <v-row class="mx-2">
            <p
              class="font-weight-black black--text"
              style="font-size: 20px; text-align: center"
            >
              Raza: <span class="font-weight-black red--text">{{ raza }}</span>
            </p>
          </v-row>
        </v-col>
        <v-col cols="3">
          <v-row class="mx-2">
            <p
              class="font-weight-black black--text"
              style="font-size: 20px; text-align: center"
            >
              Crias hembra:
              <span class="font-weight-black red--text">{{ criasH }}</span>
            </p>
          </v-row>
          <v-row class="mx-2">
            <p
              class="font-weight-black black--text"
              style="font-size: 20px; text-align: center"
            >
              Crias Macho:
              <span class="font-weight-black red--text">{{ criasM }}</span>
            </p>
          </v-row>
        </v-col>
        <v-col cols="3">
          <v-row class="mx-2">
            <p
              class="font-weight-black black--text"
              style="font-size: 20px; text-align: center"
            >
              Peso total:
              <span class="font-weight-black red--text">{{ peso }}</span>
            </p>
          </v-row>
          <v-row class="mx-2">
            <p
              class="font-weight-black black--text"
              style="font-size: 20px; text-align: center"
            >
              Peso promedio:
              <span class="font-weight-black red--text">{{ pesoProm }}</span>
            </p>
          </v-row>
        </v-col>
        <v-col cols="3">
          <v-row class="mx-2">
            <p
              class="font-weight-black black--text"
              style="font-size: 20px; text-align: center"
            >
              Cantidad de reces:
              <span class="font-weight-black red--text">{{ cantidad }}</span>
            </p>
          </v-row>
          <v-row class="mx-2">
            <p
              class="font-weight-black black--text"
              style="font-size: 20px; text-align: center"
            >
              Origen:
              <span class="font-weight-black red--text">{{ origen }}</span>
            </p>
          </v-row>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="6">
          <v-card class="car">
            <v-card-title>
              <v-row>
                <h2 class="my-4 ml-5">Datos de la subasta</h2>
                <v-row> </v-row>
                <v-spacer></v-spacer>
                <v-btn
                  @click="irTablero()"
                  outlined
                  color="red"
                  class="my-4 ml-5 mr-3"
                  >Tablero</v-btn
                >
              </v-row>
            </v-card-title>
            <v-card-text>
              <v-row>
                <v-col cols="12" md="4">
                  <v-subheader style="font-size: 22px; text-align: center"
                    >Precio Inicial</v-subheader
                  >
                </v-col>
                <v-col cols="12" md="5">
                  <v-text-field
                    style="font-size: 25px; text-align: center"
                    v-model="precioKg"
                    label="Precio Inicial"
                    value="0.0"
                    prefix="$"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="3">
                  <v-btn
                    outlined
                    color="green"
                    @click="precioInicial()"
                    v-if="estado != 2"
                    >Establecer</v-btn
                  >
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="4">
                  <v-subheader style="font-size: 22px; text-align: center"
                    >Puja</v-subheader
                  >
                </v-col>
                <v-col cols="8">
                  <v-text-field
                    style="font-size: 25px; text-align: center"
                    size="13"
                    label="Puja"
                    v-model="precioFin"
                    value="0.0"
                    prefix="$"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-divider class="mx-4 my-4"></v-divider>
              <v-row>
                <v-col cols="5" align="center">
                  <v-img
                    src="../assets/icons/subastador2.jpeg"
                    height="100"
                    width="100"
                  ></v-img>
                </v-col>
                <v-col cols="7" align-self="center">
                  <v-text-field
                    style="font-size: 25px; text-align: center"
                    label="Numero de paleta"
                    prefix="#"
                    v-model="paleta"
                  ></v-text-field>
                </v-col>
                <v-col cols="4" align-self="center" align="center"> </v-col>
              </v-row>
            </v-card-text>
            <v-card-actions>
              <v-btn
                v-if="estado != 2"
                @click="subastaDesierta()"
                outlined
                color="red"
                class="my-5 ml-5"
              >
                Subasta Desierta
              </v-btn>
              <v-spacer> </v-spacer>
              <v-btn
                v-if="estado != 2"
                class="my-5 mr-5"
                @click="adjudicar()"
                color="green"
                outlined
                >Adjudicar Subasta
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
        <v-col cols="2">
          <v-row>
            <v-col cols="12" class="text-center"><h2>Paletera</h2></v-col>
            <v-col cols="6" v-for="(p, i) in participantes" :key="i">
              <v-card class="text-center elevation-0">
                <v-avatar color="pink" size="68" @click="participante(p)">
                  <h1 class="white--text">{{ p.consecutiveholder }}</h1>
                </v-avatar>
              </v-card>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="4" class="columna mt-2">
          <v-row>
            <v-col cols="12" class="text-center">
              <v-row>
                <v-col cols="6">
                  <h2>Precios</h2>
                </v-col>
                <v-col cols="6">
                  <v-spacer></v-spacer>
                  <v-btn
                    outlined
                    color="green"
                    @click="enviarPuja()"
                    v-if="estado != 2"
                    >Enviar puja</v-btn
                  >
                </v-col>
              </v-row>
            </v-col>
            <v-col cols="4" v-for="(p, i) in preciosBotonera" :key="i">
              <v-card class="text-center elevation-0">
                <v-avatar rounded color="blue" size="68" @click="botonera(p)">
                  <h2 class="white--text">{{ precioKg + p }}</h2>
                </v-avatar>
              </v-card>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>

<script>
//import axios from "axios";
import Swal from "sweetalert2";
export default {
  name: "SaleLotCattle",
  data() {
    return {
      precioFin: 0,
      participantes: [],
      subasta: "",
      id: "",
      idSubasta: "",
      idSaleHolder: "",
      lote: "",
      estado:1,
      peso: 0,
      pesoProm:0,
      origen:"",
      cantidad: 0,
      clase: "",
      raza: "",
      criasM:0,
      criasH:0,
      precioInicia: 0,
      precioFinal: 0,
      precioTotal: 0,
      holderActual: "",
      paleta: "",
      precioKg: 7800 - 600,
      preciosBotonera: [
        100, 200, 300, 400, 500, 600, 700, 800, 900, 1000, 1100, 1200,
      ],
    };
  },

  sockets: {
    connect() {
      this.isConnected = true;
      console.log("conectado");
      this.$socket.emit("pidiendoinfoinicial", (subasta) => {
      if (!subasta) return;
      this.idSubasta = subasta.sale;
      console.log(this.idSubasta);
      this.id = subasta._id;
      this.estado=subasta.lotcattle.state
      this.lote = subasta.lotcattle.lot;
      this.peso = subasta.lotcattle.weight;
      this.pesoProm=subasta.lotcattle.weightavg
      this.participantes=subasta.holders
      console.log(this.participantes);
      this.cantidad = subasta.lotcattle.quantity;
      this.clase = subasta.lotcattle.classcattle;
      this.origen= subasta.lotcattle.origin
      this.raza = subasta.lotcattle.breed.description;
      this.criasH=subasta.lotcattle.calffemale
      this.criasM=subasta.lotcattle.calfmale
      this.precioInicia = subasta.initialprice;
      this.precioFinal = subasta.currentpricekg;
      this.precioTotal = subasta.currentprice;
      this.holderActual = subasta.currentholder;
      this.paleta = subasta.currentconsecutiveholder;
      console.log(subasta);
      
    });
    },
    disconnect() {
      this.isConnected = false;
      console.log("desconectado");
    },
  },
  methods: {
    participante(p) {
      this.paleta = p.consecutiveholder;
      this.idSaleHolder = p._id;
      console.log(p._id);
    },

    botonera(b) {
      this.precioFin = this.precioKg + b;
      console.log(this.precioFin);
    },

    adjudicar() {
     
      Swal.fire({
        title: "Cambiar de estado",
        text: "Esta seguro que desea terminar la subasta?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "green",
        cancelButtonColor: "red",
        confirmButtonText: "Si, cambiar estado",
      }).then((result) => {
        if (result.isConfirmed) {
          const idSaleLotCattle = this.id;
          const subasta = {
          idSaleLotCattle,
            };
            this.$socket.emit("adjudiquesubasta", subasta, (msg) => {
              console.log(msg);
              this.estado=msg
            });
            Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Subasta terminada",
            showConfirmButton: false,
            timer: 2000,
          });
        }
    })
  },
    precioInicial() {
      console.log(this.idSubasta);
      const idSaleLotCattle = this.id;
      const precioInicial = this.precioKg;
      const subasta = {
        idSaleLotCattle,
        precioInicial,
      };
      console.log(subasta);
      this.$socket.emit("asigneprecioinicial", subasta, (msg) => {
        console.log(msg);
      });

      /* io.emit("asigne-precio-inicial", subasta, (msg) => {
        console.log(msg);
      }); */
    },

    subastaDesierta() {
     

      Swal.fire({
        title: "Cambiar de estado",
        text: "Esta seguro que desea terminar la subasta?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "green",
        cancelButtonColor: "red",
        confirmButtonText: "Si, cambiar estado",
      }).then((result) => {
        if (result.isConfirmed) {
          const idSaleLotCattle = this.id;
          const subasta = {
            idSaleLotCattle,
          };
          this.$socket.emit("declaresubastadesierta", subasta, (msg) => {
            console.log(msg);
          });
            Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Subasta Desierta",
            showConfirmButton: false,
            timer: 2000,
          });
        }
    })
    },

    enviarPuja() {
      const idSaleLotCattle = this.id;
      console.log(this.subasta);

      const precioActual = this.precioFin;
      const holderActual = this.idSaleHolder;
      const paleta = this.paleta;
      const total = this.precioFin * this.peso;
      console.log("total "+parseInt(this.precioFin) * parseInt(this.peso));

      const subasta = {
        idSaleLotCattle,
        precioActual,
        holderActual,
        paleta,
        total,
      };
      console.log(subasta);
      this.$socket.emit("guardepuja", subasta, (msg) => {
        console.log(msg);
      });
    },

    irTablero() {
      //window.open("https://subasta-ganadera.herokuapp.com/#/tablero", "_blank");
      window.open("http://localhost:8080/#/tablero", "_blank");
    },
  },
  created() {
    
  },
};
</script>
<style>
.car {
  border: 1px solid green !important;
}
.car2 {
  border: 3px solid red !important;
}
.columna {
  border-left: 1px solid green !important;
}
</style>