import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from "axios"
import vuetify from './plugins/vuetify'
import socketio from 'socket.io-client';
import VueSocketIO from 'vue-socket.io';

Vue.config.productionTip = false

export const SocketInstance = socketio('https://subasta-ganadera.herokuapp.com', { transports: ["websocket", "polling", "flashsocket"] });

Vue.use(VueSocketIO, SocketInstance)

axios.defaults.baseURL="https://subasta-ganadera.herokuapp.com/api/"

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')

router.beforeEach((to, from, next) =>{
  if (!store.state.token && to.path != "/" && to.path != "/resetear"){
    next("/");
  }else{
    next();
  } 
}) 