<template>
  <v-app class="mt-16">
    <v-container class="mb-16" v-if="$store.state.token !== ''">
      <v-row>
        <v-col cols="6">
          <v-card class="elevation-0">
            <h2>Lotes a subastar</h2>
          </v-card>
        </v-col>
        <v-col cols="5">
          <v-card class="elevation-0 d-flex justify-end">
            <v-btn
              class="elevation-5"
              @click="nuevoLote()"
              outlined
              color="green"
              v-if="$store.state.subasta.state!=='2'"
              >Nuevo lote</v-btn
            >
          </v-card>
        </v-col>
        <v-col cols="1">
          <v-card class="elevation-0 d-flex justify-end">
            <v-btn
              class="elevation-5"
              @click="crearPDF()"
              outlined
              color="green"
            >
              <v-icon>mdi-printer</v-icon>
            </v-btn>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="4" v-for="(l, i) in lotes" :key="i">
          <v-card class="mx-auto" max-width="300">
            <v-card-title>Subasta No. {{ l.sale.salenumber }}</v-card-title>
            <v-card-text>
              <v-row align="center" class="mx-0">
                <div class="grey--text ms-4">Origen {{ l.origin }}</div>
              </v-row>
              <div class="my-4 text-subtitle-1">
                <span>Cantidad de cabezas {{ l.quantity }}</span>
              </div>
              <div my-4 text-subtitle-1>
                <span > Clase de ganado {{ l.classcattle }}</span>
              </div>
              <div  my-4 text-subtitle-1>
                <span> Raza{{ l.breed.description }}</span>
              </div>
            </v-card-text >
            <v-divider class="mx-4"></v-divider>
            <v-card-title
              >Lote No. {{ l.lot }}
              <v-spacer></v-spacer>
              <template>
                <span class="green--text" text v-if="l.state == '0'"
                  >Inactivo</span
                >
                <span class="green--text" text v-if="l.state == '1'"
                  >Activo</span
                >
                <span class="orange--text" text v-if="l.state == '3'"
                  >Subasta en proceso</span
                >
                <span class="red--text" text v-if="l.state == '2'">Subastado</span>
              </template>
            </v-card-title>
            <v-card-actions>
              <v-btn color="green" outlined @click="subastar(l)" v-if="l.state == '1' || l.state == '3'">
                Subastar
              </v-btn>
              <v-spacer></v-spacer>
              <v-icon class="mr-2" color="green" @click="editar(l)"
              v-if="l.state == '1' || l.state == '3'">mdi-pencil</v-icon
              >
              <template v-if="l.state == '0' || l.state == '1'">
                <v-icon
                  class="mr-2"
                  color="green"
                  @click="activarDesactivar(l)"
                  v-if="l.state == '0'"
                  >mdi-eye</v-icon
                >
                <v-icon
                  class="mr-2"
                  color="red"
                  @click="activarDesactivar(l)"
                  v-else
                  >mdi-eye-off</v-icon
                >
              </template>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
    <v-dialog v-model="dialog" persistent max-width="600px">
      <v-toolbar dark color="green">
        <v-btn icon dark @click="cerrarDialog()">
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <v-toolbar-title>{{ titulo }}</v-toolbar-title>
      </v-toolbar>
      <v-card>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-select
                  label="Proveedor..."
                  :items="holders"
                  v-model="proveedor"
                  item-value="value"
                ></v-select>
              </v-col>
              <v-col cols="12" sm="6" v-if="bd === false">
                <v-text-field
                  label="Subasta"
                  v-model="subasta"
                  disabled
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field label="Origen" v-model="origen"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field
                  label="Cantidad"
                  v-model="cantidad"
                  type="number"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field
                  label="Clase de ganado"
                  v-model="clase"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field
                  label="Peso"
                  v-model="peso"
                  type="number"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field
                  label="Crias Hembra"
                  v-model="criasH"
                  type="number"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field
                  label="Crias Macho"
                  v-model="criasM"
                  type="number"
                ></v-text-field>
              </v-col>

              <v-col cols="12" sm="6">
                <v-text-field label="ICA" v-model="ica"></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-select
                  label="Razas..."
                  :items="razas"
                  v-model="raza"
                  item-value="value"
                ></v-select>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="elevation-3"
            outlined
            color="red"
            @click="cerrarDialog()"
          >
            Cerrar
          </v-btn>
          <v-btn
            class="elevation-3"
            outlined
            color="green"
            @click="accionLote()"
          >
            {{ etiqueta }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";
import jsPDF from "jspdf";
import "jspdf-autotable";
export default {
  name: "LotCattle",
  data() {
    return {
      dialog: false,
      bd: false,
      titulo: "Guardar Lote",
      etiqueta: "Gurdar",
      subasta: this.$store.state.subasta.salenumber,
      idSubasta: this.$store.state.subasta._id,
      idLote: "",
      proveedor: "",
      origen: "",
      cantidad: 0,
      clase: "",
      peso: 0,
      criasM: 0,
      criasH: 0,
      raza: "",
      ica: "",
      lotes: [],
      lotesPuja: [],
      holders: [],
      razas:[],
      subastas: [],
    };
  },

  sockets: {
    connect() {}
  },
  methods: {
    listarLotes() {
      console.log(this.$store.state.subasta);
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .get(`lotcattle/subasta/${this.idSubasta}`, header)
        .then((res) => {
          console.log(res);
          this.lotes = res.data.cattleLot;
        })
        .catch((error) => {
          console.log(error);
        });
    },

    listarLotesPuja() {
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .get(`lotcattle/puja/${this.idSubasta}`, header)
        .then((res) => {
          console.log(res);
          this.lotesPuja = res.data.cattleLot;
        })
        .catch((error) => {
          console.log(error);
        });
    },

    listarProveedores() {
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .get("holder", header)
        .then((response) => {
          console.log(response);
          response.data.holder.forEach((h) => {
            this.holders.push({
              text: " documento: " + h.document + " - " + h.name,
              value: h._id,
            });
          });
        })
        .catch((error) => {
          console.log(error);
        });
    },

    listarRazas() {
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .get("breed", header)
        .then((response) => {
          console.log(response);
          response.data.breed.forEach((h) => {
            this.razas.push({
              text: h.description,
              value: h._id,
            });
          });
        })
        .catch((error) => {
          console.log(error);
        });
    },

    subastar(lote) {
      console.log(lote);
      this.$store.dispatch("setLote", lote);
      localStorage.setItem("lote",lote._id)
      localStorage.setItem("subasta",lote.sale._id)
      console.log(lote.state);
      if (lote.state == 1) {
        let header = { headers: { "x-token": this.$store.state.token } };
        axios
          .post(
            "salelotcattle",
            {
              lotcattle: lote._id,
            },
            header
          )
          .then((res) => {
            console.log(res);
            Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Actualizacin exitosa",
            showConfirmButton: false,
            timer: 2000,
          });
         
          //window.open("https://subasta-ganadera.herokuapp.com/#/saleLotCattle", "_blank");
          window.open("http://localhost:8080/#/saleLotCattle", "_blank");
          })
          .catch((err) => {
            console.log(err);
            Swal.fire({
            title: "<strong>Actualizacin rechazada</strong>",
            icon: "error",
            html: `<h4>${err.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
          });
      } else if (lote.state == 3) {
        //window.open("https://subasta-ganadera.herokuapp.com/#/saleLotCattle", "_blank");
        window.open("http://localhost:8080/#/saleLotCattle", "_blank");
      }
    },

    editar(lote) {
      console.log(lote);
      this.dialog = true;
      this.bd = true;
      this.idLote = lote._id;
      this.etiqueta = "Editar";
      this.titulo = "Editar Lote";
      this.proveedor = lote.provider._id;
      this.origen = lote.origin;
      this.cantidad = lote.quantity;
      this.criasH = lote.calffemale;
      this.criasM = lote.calfmale;
      this.clase = lote.classcattle;
      this.ica = lote.ica;
      this.peso = lote.weight;
    },

    nuevoLote() {
      this.dialog = true;
      this.bd = false;
      this.etiqueta = "Guardar";
      this.titulo = "Guardar Lote";
    },

    cerrarDialog() {
      this.dialog = false;
      this.limpiarCajas();
    },

    activarDesactivar(item) {
      console.log(item._id);
      Swal.fire({
        title: "Cambiar de estado",
        text: "Esta seguro que desea cambiar el estado del usuario?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "green",
        cancelButtonColor: "red",
        confirmButtonText: "Si, cambiar estado",
      }).then((result) => {
        if (result.isConfirmed) {
          if (item.state === "1") {
            let header = { headers: { "x-token": this.$store.state.token } };
            axios
              .put(`lotcattle/unactivate/${item._id}`, {}, header)
              .then((response) => {
                console.log(response);
                this.listarLotes();
                Swal.fire("Ok", "El usuario ha cambiado de estado", "success");
              })
              .catch((error) => {
                console.log(error.response);
                Swal.fire("Error", "No se pudo cambiar el estado", "error");
              });
          } else {
            let header = { headers: { "x-token": this.$store.state.token } };
            axios
              .put(`lotcattle/activate/${item._id}`, {}, header)
              .then((response) => {
                console.log(response);
                this.listarLotes();
                Swal.fire("Ok", "El usuario ha cambiado de estado", "success");
              })
              .catch((error) => {
                console.log(error.response);
                Swal.fire("Error", "No se pudo cambiar el estado", "error");
              });
          }
        }
      });
    },

    accionLote() {
      if (this.bd === true) {
        console.log("estoy editando..");
        this.editarLote();
      } else {
        console.log("estoy agregando..");
        this.agregarLote();
      }
    },

    editarLote() {
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .put(
          `lotcattle/${this.idLote}`,
          {
            provider: this.proveedor,
            origin: this.origen,
            quantity: this.cantidad,
            classcattle: this.clase,
            weight: this.peso,
            calfmale: this.criasH,
            calffemale: this.criasM,
            breed: this.raza,
            ica: this.ica,
          },
          header
        )
        .then((res) => {
          console.log(res);
          Swal.fire("Ok", "Actulizacion exitosa", "success");
          this.listarLotes();
          this.listarLotesPuja();
          this.limpiarCajas();
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Actualizacin rechazada</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
        });
    },

    agregarLote() {
      let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .post(
          "lotcattle",
          {
            sale: this.idSubasta,
            provider: this.proveedor,
            origin: this.origen,
            quantity: this.cantidad,
            classcattle: this.clase,
            weight: this.peso,
            calfmale: this.criasH,
            calffemale: this.criasM,
            breed: this.raza,
            ica: this.ica,
          },
          header
        )
        .then((res) => {
          console.log(res);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Actualizacin exitosa",
            showConfirmButton: false,
            timer: 2000,
          });
          this.listarLotes();
          this.listarLotesPuja();
          this.limpiarCajas();
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Actualizacin rechazada</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
        });
    },

    limpiarCajas() {
      this.proveedor = "";
      this.origen = "";
      this.cantidad = "";
      this.criasH = "";
      this.criasM = "";
      this.clase = "";
      this.raza = "";
      this.ica = "";
      this.peso = "";
    },

    fechaSalida(r) {
      let d = new Date(r);
      return d.toLocaleDateString() + " - " + d.toLocaleTimeString();
    },

    crearPDF() {
      var columns = [
        { title: "Lote #", dataKey: "lote" },
        { title: "Origen", dataKey: "origen" },
        { title: "Raza", dataKey: "raza" },
        { title: "Cantidad", dataKey: "cantidad" },
        { title: "Crias de hembras", dataKey: "criasH" },
        { title: "Crias de machos", dataKey: "criasM" },
        { title: "Peso", dataKey: "peso" },
        { title: "Peso promedio", dataKey: "pesoProm" },
        { title: "Fecha llegada", dataKey: "creado" },
      ];
      var rows = [];

      this.lotesPuja.map(function (x) {
        let d = new Date(x.createdAt);
        let fecha=d.toLocaleDateString() + " - " + d.toLocaleTimeString();
        rows.push({
          lote: x.lot,
          origen: x.origin,
          raza: x.breed.description,
          cantidad: x.quantity,
          criasH: x.calffemale,
          criasM: x.calfmale,
          peso: x.weight,
          pesoProm: x.weightavg,
          creado:fecha
        });
      });
      var doc = new jsPDF("l", "pt");
      doc.autoTable(columns, rows, {
        margin: { top: 60, left: 30 },
        addPageContent: function () {
          doc.text("Lista de lotes", 30, 30);
        },
      });

      doc.save("Lotes.pdf");
    },
  },
  mounted() {
    this.listarLotes();
    this.listarProveedores();
    this.listarRazas()
    this.listarLotesPuja();
  },
};
</script>
